.article{position:relative;width:100%;max-width:750px;min-height:200px;display:flex;background-color:#fff;border-radius:10px}.article__content{position:relative;display:flex;flex-grow:2;flex-direction:column;padding:0 20px;z-index:10;background-color:#fff;border-radius:10px 0 0 10px}.article__content::after{position:absolute;content:"";top:0;right:0;width:20px;height:100%;background-color:#fff;transform:skewX(6deg) translateX(5px)}.article__content__title{width:fit-content;max-width:500px;text-align:left;margin-bottom:5px}.article__content__source{position:relative;color:#969696;transition:all .4s}.article__content__source::after{position:absolute;left:5px;bottom:-5px;content:"";width:50px;height:4px;background-color:#285aff;border-radius:2px;transform:skewX(-45deg);transition:all .4s}.article__content__text{width:100%;text-align:left;flex-grow:1;max-width:500px}.article__content__link{position:relative;width:fit-content;align-self:flex-end;text-decoration:none;color:#285aff;font-weight:700;transition:all .4s;margin-bottom:15px}.article__content__link::after{position:absolute;right:0;content:"â†’";color:rgba(0,0,0,0)}.article__content__link:hover{padding-right:15px;color:#ff3628}.article__content__link:hover::after{color:#ff3628}.article__img{width:150px;min-width:100px;flex-grow:1;border-radius:0 10px 10px 0;background-repeat:no-repeat;background-size:cover;background-position:center}.article__img-picture{width:300px}.article__list{position:absolute;display:flex;flex-direction:column;width:50%;height:100%;padding:0;margin:0;background-color:rgba(0,0,0,.7);border-radius:10px;color:rgba(0,0,0,0);list-style:none;transition:all .4s;z-index:5}.article__list__item{align-self:end;text-align:end;padding:10px 20px 0 0}.reverse{flex-direction:row-reverse}.reverse .article__content{border-radius:0 10px 10px 0}.reverse .article__content::after{left:-10px}.reverse .article__img{border-radius:10px 0 0 10px}.reverse .article__list__item{align-self:start;text-align:start;padding:10px 0 0 20px}.article:hover .article__list{width:100%;color:#fff}.article:hover .article__content__source::after{background-color:#ff3628;transform:skewX(50deg)}